export default {
  name: "Header",
  props: {
    title: {
      type: String,
      required: true,
    },
    title_animate: {
      type: Boolean,
      required: true,
    },
    subtitle1: {
      type: String,
      required: true,
    },
    main_heading: {
      type: String,
      required: true,
    },
    subtitle2: {
      type: String,
      required: true,
    },
    login_button: {
      type: String,
      required: true,
    },
    login_button_redirect_url: {
      type: String,
      required: true,
    },
    signup_button: {
      type: String,
      required: true,
    },
    signup_button_redirect_url: {
      type: String,
      required: true,
    },
    small_term_text: {
      type: String,
      required: true,
    },
    small_term_text_link: {
      type: String,
      required: true,
    },
    small_term_redirect_url: {
      type: String,
      required: true,
    },
    promo_title_animate: {
      type: Boolean,
      required: true,
    },
    images: {
      type: Object,
      required: true,
    },
  },
  data: {
    title_animate: true,
  },
  computed: {
    cssBackground: function () {
      return {
        '--bg-image': `url('${this.images.promo_bg_desktop}')`,
        '--bg-image-m': `url('${this.images.promo_bg_mobile}')`,
        '--game-logo': `url('${this.images.promo_game_logo}')`
      }
    }
  }
};
